@import url("https://fonts.googleapis.com/css?family=Titillium+Web:300,400,400i,700,700i&subset=latin-ext");
body {
  font-size: 18px;
  line-height: 26px;
  color: #384333;
  font-family: "Titillium Web", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  text-rendering: auto;
  overflow-x: hidden;
  overflow-y: auto; }

.content-width {
  width: 96%;
  max-width: 1340px;
  display: block;
  margin: auto; }
  .content-width.thin {
    max-width: 800px; }

.form-row {
  margin-bottom: 20px; }
  .form-row.half {
    float: left;
    width: 48%; }
    .form-row.half:nth-of-type(2n+2) {
      float: right; }
  .form-row:last-of-type {
    margin-bottom: 0; }
  .form-row.button-row {
    margin-top: 40px; }
  .form-row label {
    font-weight: 700;
    margin-bottom: 5px;
    display: inline-block; }
  .form-row .checkfield {
    margin-right: 30px; }
    .form-row .checkfield:last-child {
      margin-right: 0px; }

.address-row .zip-row {
  width: 85px;
  float: left; }

.address-row .city-row {
  float: right;
  width: calc(100% - 105px); }

.clear {
  display: block;
  float: none;
  clear: both;
  width: 0;
  height: 0; }

.like-h1,
.like-h2,
.like-h3,
h1,
h2,
h3 {
  font-weight: 700;
  margin: 0; }

.like-h1,
h1 {
  font-size: 42px;
  line-height: 52px; }

.like-h2,
h2 {
  font-size: 34px;
  line-height: 44px; }

.like-h3,
h3 {
  font-size: 26px;
  line-height: 36px; }

a {
  color: #384333;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

@media screen and (max-width: 1023px) {
  .content-width {
    width: 92%; }
  .like-h1,
  h1 {
    font-size: 36px;
    line-height: 46px; }
  .like-h2,
  h2 {
    font-size: 28px;
    line-height: 38px; }
  .like-h3,
  h3 {
    font-size: 20px;
    line-height: 30px; } }

@media screen and (max-width: 600px) {
  h1 {
    font-size: 32px;
    line-height: 38px; }
  .like-h2,
  h2 {
    font-size: 24px;
    line-height: 30px; }
  .like-h3,
  h3 {
    font-size: 18px;
    line-height: 24px; }
  .form-row {
    margin-bottom: 15px; }
    .form-row.half {
      width: 100%;
      float: none; }
    .form-row.button-row .button {
      display: block;
      width: 100%; } }



